<template>
  <section class="container">
    <article class="item">
      <h1 class="logo">
        <img src="../../assets/images/LOGO.png" alt="LOGO">
      </h1>
    </article>
    <article class="text">
      <h2>每个人都有一颗向好之心</h2>
      <p>2017年6月，插坐学院完成战略调整，从企业新媒体培训机构，升级为综合型企业培训服务商，同时推出企业在线培训平台 —— 好多课 APP，专注移动互联网时代的学习体验，通过线上线下结合的方式，让企业培训管理更简单、员工学习效果更直观。</p>
    </article>
    <article class="end">
      <p>截至目前，已累计服务30万+在线学员，覆盖超过15000+家企业客户。</p>
    </article>
  </section>
</template>

<script>
  export default {
    data() {
      return {
        navIndex: 2
      }
    },
    created() {
      document.title = `一站式企业内训服务商 - 插座学院`
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .container {
    width: 75%;
    margin: 0 auto;
    overflow: hidden;
    min-height: 100vh;
    background-color: #FFFFFF;
    margin-bottom: -295px;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 380px;
    }
  }

  .item {
    width: 100%;
    padding: 48px 0;
    text-align: center;
    background: white;
  }

  .logo {
    width: 360px;
    margin: 0 auto;

    & img {
      width: 100%;
    }
  }

  a {
    color: #409EFF;
  }

  .text {
    padding: 0 24px 24px;
    line-height: 42px;
    font-size: 20px;
    color: #666;
    text-indent: 2em;
  }

  .content {
    padding-top: 24px;
  }

  .end {
    line-height: 42px;
    font-size: 20px;
    color: #666;
    text-indent: 2em;
  }

  @media screen and (min-width: 200px) and (max-width: 750px) {
    .container {
      width: 100%;
    }
  }
</style>



